import React from "react";
import styled from "styled-components";
import { useMediaQuery } from "react-responsive";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Footer = () => {
    const isMobile = useMediaQuery({ query: "(max-width: 680px)" });

    const copiedText = async (text) => {
        try {
            await navigator.clipboard.writeText(text);
            toast.success("  .", {
                icon: ({ theme, type }) => (
                    <img
                        src="MYLOGO.png"
                        alt="mylogo"
                        style={{
                            width: "4vmin",
                            height: "4vmin",
                        }}
                    />
                ),
            });
        } catch (e) {
            toast.error(".  !");
        }
    };

    return (
        <div className="inner" style={{ fontSize: "3.5em" }}>
            <ToastContainer
                autoClose={3000}
                icon={false}
                style={{ fontSize: "0.38em" }}
            />
            <Container>
                <div>{"<Footer>"}</div>
                <ProfileWrapper>
                    <HalfWrapper>
                        {isMobile && (
                            <MainTitle>
                                <WrapperTitle>
                                    <PointTitle>"</PointTitle>  
                                    
                                </WrapperTitle>
                                <WrapperTitle>
                                    <PointTitle> </PointTitle> {" "}
                                    <PointTitle> </PointTitle>
                                </WrapperTitle>{" "}
                                <WrapperTitle>
                                    {" "}
                                       .{" "}
                                    <PointTitle>"</PointTitle>
                                </WrapperTitle>
                            </MainTitle>
                        )}
                        <ProfileImage src="imgs/photo.png" alt="person" />
                        <NameTitle>
                             {" "}
                            <PointTitle>    </PointTitle>
                        </NameTitle>
                        <ContactWrapper>
                            #Contact
                            <IconsWrapper>
                                <Icon
                                    src="imgs/kakaotalk.png"
                                    alt="kakaotalk"
                                    onClick={(e) =>
                                        copiedText(
                                            process.env.REACT_APP_CONTACT_KAKAO
                                        )
                                    }
                                />
                                <Icon
                                    src="imgs/email.png"
                                    alt="email"
                                    onClick={() =>
                                        copiedText(
                                            process.env.REACT_APP_CONTACT_EMAIL
                                        )
                                    }
                                />
                            </IconsWrapper>
                            #Links
                            <IconsWrapper>
                                <Icon
                                    src="imgs/github.png"
                                    alt="github"
                                    onClick={() =>
                                        window.open(
                                            process.env
                                                .REACT_APP_CONTACT_GITHUB_URL
                                        )
                                    }
                                />
                                <Icon
                                    src="imgs/blog.png"
                                    alt="blog"
                                    onClick={() =>
                                        window.open(
                                            process.env
                                                .REACT_APP_CONTACT_BLOG_URL
                                        )
                                    }
                                />
                                <Icon
                                    src="imgs/Instagram.png"
                                    alt="Instagram"
                                    onClick={() =>
                                        window.open(
                                            process.env
                                                .REACT_APP_CONTACT_INSTAGRAM
                                        )
                                    }
                                />
                            </IconsWrapper>
                        </ContactWrapper>
                    </HalfWrapper>
                    {!isMobile && (
                        <TitleWrapper>
                            <MainTitle>
                                <WrapperTitle>
                                    <PointTitle>"</PointTitle>  
                                    
                                </WrapperTitle>
                                <WrapperTitle>
                                    <PointTitle> </PointTitle> {" "}
                                    <PointTitle> </PointTitle>
                                </WrapperTitle>{" "}
                                <WrapperTitle>
                                    {" "}
                                       .{" "}
                                    <PointTitle>"</PointTitle>
                                </WrapperTitle>
                            </MainTitle>
                            <MentionsWrapper>
                                !    .{" "}
                                <br />
                                    
                                .
                                <br />
                                <br /> 1 8   ,
                                      
                                .
                                <br />
                                <br />
                                <HightLight>
                                      ?    
                                    .
                                </HightLight>
                                <br />
                                   6  3 
                                {" "}
                                <HightLight>
                                       .
                                </HightLight>{" "}
                                <br />
                                ! <br />
                                <HightLight>
                                     {" "}
                                    <PointTitle className="comment">
                                         
                                    </PointTitle>{" "}
                                      !
                                </HightLight>
                                <HightLight>
                                    {" "}
                                    <PointTitle className="comment">
                                          {"&"}  
                                          
                                    </PointTitle>{" "}
                                </HightLight>
                                <HightLight>
                                          
                                    .
                                </HightLight>{" "}
                            </MentionsWrapper>
                        </TitleWrapper>
                    )}
                </ProfileWrapper>
                <div>{"</Footer>"}</div>
            </Container>
        </div>
    );
};

export default Footer;

const Container = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 85vh;
    padding-bottom: 7vh;
    font-family: "East Sea Dokdo", sans-serif;
    @media screen and (max-width: 680px) {
        height: 100vh;
        justify-content: center;
    } ;
`;

const ProfileImage = styled.img`
    height: 35vmin;
`;

const ProfileWrapper = styled.div`
    width: 80vmin;
    height: 63vh;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5vh 0;
    @media screen and (max-width: 680px) {
        justify-content: center;
    } ;
`;

const TitleWrapper = styled.div`
    width: 48vmin;
    height: 63vh;
    word-break: keep-all;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.5em;
    padding: 10vh 0;
`;

const HalfWrapper = styled.div`
    height: 63vh;
    word-break: keep-all;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 0.5em;
    border-right: 1px solid black;
    padding-right: 1.7vw;
    @media screen and (max-width: 680px) {
        border-right: none;
        padding-right: 0;
    } ;
`;

const WrapperTitle = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    word-break: keep-all;
`;

const PointTitle = styled.div`
    font-size: 1.3em;
    margin: 0 0.4vw;
    color: orangered;
    &.comment {
        font-size: 1.1em;
    }
`;

const NameTitle = styled.div`
    width: 100%;
    height: 5vh;
    border: 1px dashed black;
    border-radius: 75px;
    background-color: #ffefba8a;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    font-size: 0.7em;
    margin: 1vh 0;
    padding: 0 0.4vw;
`;

const ContactWrapper = styled.div`
    width: 100%;
    height: 30vh;
    border-top: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 0 0.4vw;
    @media screen and (max-width: 680px) {
        height: 20vh;
        touch-action: none;
    } ;
`;

const IconsWrapper = styled.div`
    display: flex;
    justify-content: space-evenly;
    margin: 1vh 0;
`;

const Icon = styled.img`
    height: 4.5vh;
    cursor: pointer;
    @media screen and (max-width: 680px) {
        height: 3.5vh;
    } ;
`;
const MainTitle = styled.div`
    width: 100%;
    height: 12vh;
    border-radius: 75px;
    background-color: #ffefba8a;
    border: 1px dashed black;
    word-break: keep-all;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 0.7em;
    @media screen and (max-width: 680px) {
        width: 80vmin;
        margin: 1.2vh 0;
        font-size: 0.7em;
    } ;
    @media screen and (max-width: 380px) {
        font-size: 0.65em;
    }
} ;
`;
const MentionsWrapper = styled.div`
    height: 47vh;
    border-top: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 0.7em;
    word-break: keep-all;
    margin: 1.2vh 0;
    padding: 1vh 0.3vw;
`;

const HightLight = styled.span`
    background: linear-gradient(to top, #ffefba 50%, transparent 50%);
    display: flex;
`;
